import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    idsong: undefined,
};

export const SongIdStore = createSlice({
    name: 'song', // Tn ca slice, mi slice t 1 tn khc nhau  phn bit
    initialState,
    // Reducers cha cc hm x l cp nht state
    reducers: {
        updateIdSong(state, action) {
            state.idsong = action.payload;
        },
    },
});

// Export action ra  s dng cho tin.
export const { updateIdSong } = SongIdStore.actions;

// Action l 1 hm tr v object dng {type, payload}, chy th console.log(updateIdSong())  xem chi tit

// Hm gip ly ra state mong mun.
// Hm ny c 1 tham s l root state l ton b state trong store, chy th console.log(state) trong ni dung hm  xem chi tit
export const selectIdSong = (state) => state.song.idsong;

// Export reducer  nhng vo Store
export default SongIdStore.reducer;
